<template>
<v-app style=" background-image: url('/img/icons/backcolor.png')">
    <v-app-bar app color="transparent" dark >
    <div class="d-flex align-center">
        <v-img
        alt="Vuetify Logo"
        class="shrink mr-2"
        contain
        src="/img/icons/noteicn.png"
        transition="scale-transition"
        width="40"
        />
    </div>

    <v-spacer></v-spacer>
        <v-btn color="#FFD400" dark @click="logoutWithKakao" >LOG OUT
        <v-icon dark center></v-icon>
        </v-btn>
    <v-btn text>
        <span></span>
        <v-icon color="white">mdi-dots-vertical</v-icon>
    </v-btn>
    </v-app-bar>

    <v-main>
    <TodoHeader />
    <TodoInput v-on:addTodo="addTodo"></TodoInput>
    <TodoList
        v-bind:propsdata="todoItems" 
        @removeTodo="removeTodo"
        @editTodo="editTodo"
        @detailTodo="detailTodo"
        @toggleComplete="toggleComplete"
    ></TodoList>
    </v-main>
    
    <v-footer color="transparent" dark>
    <TodoFooter v-on:removeAll="clearAll" />
    </v-footer>
</v-app>
</template>

<script>
import TodoFooter from "../components/TodoFooter.vue";
import TodoHeader from "../components/TodoHeader.vue";
import TodoList from "../components/TodoList.vue";
import TodoInput from "../components/TodoInput.vue";



export default {
name: "App",

components: {
    TodoList,
    TodoFooter,
    TodoHeader,
    TodoInput,
},

data() {
    return {
    name:""
    };
},
methods: {
    clearAll() {
    localStorage.clear();
    this.todoItems = [];
    },
    addTodo(todoItem) {
      //localStorage.setItem(todoItem, JSON.stringify(value));//
    localStorage.setItem(todoItem,todoItem)

    this.todoItems.push(todoItem);
    },
    removeTodo(todoItem, index) {
    localStorage.removeItem(todoItem);
    this.todoItems.splice(index, 1);
    },
    editTodo(todoItem,index,text) {
    console.log(todoItem,index,text)
    this.todoItems.splice(index,1,text)
    localStorage.setItem(text,text)
    localStorage.removeItem(todoItem,todoItem)
    },
    detailTodo(value,index,todoItem){
    console.log(value,index,todoItem)
    this.todoItems.splice(index,1,todoItem)
    localStorage.setItem(todoItem,value)
    },
    toggleComplete(todoItem){
    todoItem.completed = !todoItem.completed;
    localStorage.setItem(todoItem.item,JSON.stringify(todoItem))
    },
    logoutWithKakao() {
        console.log(window.Kakao.Auth.getAccesstoken())

    }
},
created() {
    if (localStorage.length > 0) {
    for (var i = 0; i < localStorage.length; i++) {
        this.todoItems.push(localStorage.key(i));
    }
    }
},
}

</script>

